.info {
    &__wrapper {
        position: absolute;
        top: 0;
        right: 0;
        width: 600px;
        margin: 5px;
        display: flex;
        flex-flow: column;  
        z-index: 1000;
    }

    &__title {
        color: #fff;
        text-align: right;
        padding: 0 15px;
        line-height: 1em;
    }
    &__line {
        border: 3px solid #fff;
        border-radius: 20%;
        width: 100%;
        margin: 20px 0;
    }
    &__text {
        padding: 5px;
        margin-left: 85px;
        color: #fff;
        font-size: 18px;
        text-align: justify;
        text-indent: 25px;
        width: 500px;
    }
}
