* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

@font-face {
  font-family: "Stardate";
  src: url(./fonts/Peace_Sans.otf);
}

h1 {
  font-family: Stardate;
  color: #fff;
  font-size: 80px;
}

.app {
  &__wrapper {
    width: 100%;
    height: 100vh;
    background-color: #000;
  }
}

.annotation {
  transform: translate3d(calc(50%), calc(-50%), 0);
  border: 1px solid transparent;
  text-align: center;
  background: #181c20;
  color: #8c92a4;
  padding: 10px 15px;
  border-radius: 5px;
  user-select: none;
  transition: all .5s;
  cursor: pointer;

  &::before {
    content: "";
    position: absolute;
    top: 25px;
    left: -25px;
    height: 2px;
    width: 25px;
    background: #181c20;
  }

  &:hover {
    border: 1px solid #fff;
  }
}
