.menu {
    position: absolute;
    top: 0px;
    margin: 15px;
    gap: 5px;
    display: flex;

    &__btn {
        width: 130px;
        height: 50px;
        border-radius: 3%;
        display: flex;
        align-items: center;
        justify-content: center;
        background-color: #fff;
        font-size: 20px;
        user-select: none;
        cursor: pointer;
        transition: all 0.2s;
        &:hover {
            background-color: #ffffffb9;
        }
    }
}